___CSS_LOADER_EXPORT___.push([module.id, `html {
  height: fit-content;
}
body {
  margin: 0;
  padding: 0;
  height: fit-content;
  overflow-x: hidden;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
#content {
  width: 100%;
  height: fit-content;
  height: 100vh;
}
#nav {
  width: 100%;
  height: 100%;
}
.mainBut {
  background-position: center;
  background-size: cover;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}
button {
  border: none;
  padding: 10px;
  width: 100px;
  font-weight: 700;
  background: none;
  color: azure;
  opacity: 0.6;
  transition: 500ms;
}
button:hover {
  opacity: 1;
}
.active {
  opacity: 1;
}
#footer {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  bottom: 0;
  height: 100px;
  width: 100%;
  color: white;
  background-position: top;
  background-size: auto;
}
#atag {
  color: #5075d9;
  text-decoration: none;
  transition: 0.5s;
}
#atag:hover {
  color: #385194;
}
#menu,
#about {
  background-color: #f0bc5d;
  width: 100%;
  display: flex;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 0;
}
#list {
  width: 100%;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 0px 30px 10px;
  border: 1px solid #385194;
}
#workTime {
  width: 100%;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 30px 0px 30px 10px;
  border: 1px solid #385194;
}
li {
  font-weight: 600;
  padding: 10px;
  text-transform: uppercase;
  color: #2a3e75;
}
.hidden {
  display: none !important;
}
.container {
  display: block;
}

#text {
  margin-top: 0;
  width: 70%;
  padding-top: 30px;
  padding-bottom: 50px;
  text-align: center;
  font-size: 10px;
  text-overflow: ellipsis;
  color: #2a3e75;
}
#contact {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f0bc5d;
  height: 100%;
  font-weight: 900;
  color: #2a3e75;
}
